.search {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 80px;
}

.search__title {
  color: #fff;
  font-family: "Roboto Slab";
  font-size: 60px;
  font-weight: 400;
  line-height: 1.2;
  width: 608px;
  height: 128px;
  margin: 80px 416px 32px;
}

.search__explaination {
  color: #fff;
  font-family: Roboto;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.2;
  width: 608px;
  margin: 0 416px 88px;
}

.search__form {
  display: flex;
  justify-content: center;
  align-items: center;
}

.search__input {
  position: absolute;
  right: 416px;
  color: #000;
  width: 608px;
  height: 64px;
  fill: #fff;
  stroke-width: 1px;
  stroke: var(--simulator-gray-divider, #d1d2d6);
  backdrop-filter: blur(4px);
  border-radius: 100px;
  border: none;
  text-indent: 24px;
}

.search__button {
  position: relative;
  left: 220px;
  width: 168px;
  height: 66px;
  border-radius: 100px;
  background: #2f71e5;
  box-shadow: 0px 5px 15px 0px rgba(14, 26, 57, 0.2);
  border: none;
  color: var(--main-white, #fff);
  text-align: center;
  font-family: Roboto;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.2;
  cursor: pointer;
}

.search__input::placeholder {
  color: #b6bcbf;
  font-family: Roboto;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
}

@media screen and (max-width: 768px) {
  .search {
    align-items: flex-start;
    margin-bottom: 48px;
  }

  .search__title {
    font-size: 36px;
    width: 385px;
    height: 80px;
    margin: 30px 163px 16px;
  }

  .search__explaination {
    width: 452px;
    margin: 0 160px 48px 156px;
  }

  .search__input {
    width: 448px;
    height: 56px;
    right: 140px;
  }

  .search__button {
    width: 160px;
    height: 56px;
    left: 427px;
  }
}

@media screen and (max-width: 320px) {
  .search {
    align-items: flex-start;
    margin-bottom: 48px;
  }

  .search__title {
    font-size: 36px;
    width: 285px;
    height: 80px;
    margin: 30px 19px 16px;
  }

  .search__explaination {
    width: 288px;
    margin: 0 19px 122px;
  }

  .search__input {
    width: 288px;
    height: 56px;
    top: 376px;
    left: 16px;
  }

  .search__button {
    width: 288px;
    height: 56px;
    top: 102px;
    left: 16px;
  }
}
